import React from "react";
import './login.css'

const Login = () => {
  return (
    <div className="showcase login">
      <div className="overlay">
        <div className="form">
          <input
            type="email"
            name="name"
            placeholder="Enter your email"
            required
          />

          <input
            type="password"
            name="password"
            placeholder="Enter your password"
            required
          />
          <button type="submit">Login</button>
        </div>
      </div>
    </div>
  );
};

export default Login;
